import React from 'react';
import Navbar from '../components/Navbar';
import '../styles/global.scss';

export default function Article() {
  return (
    <body>
      <head>
        <meta charSet="utf-8" />
        <title>Maker X Modeler</title>
      </head>

      <Navbar />
      
      <main>
        <section className="section">
          <div className="container">
            <h1 className="title has-text-centered">Maker X Modeler: Discussing Airbrushes</h1>
            <p>When it comes to painting a plastic model via airbrush, you generally hear the advice that you should "thin your paints and lower your air pressure", right? And there's plenty of people who do that. In an attempt to re-examine this theory, and to find a different method for crafting beautiful models, the editorial team invited three modelers who have worked with this magazine before. Apart from discussing about how they reached their current painting style, we'll be examining the various companies involved with modeling paints.</p>
            <p>Why not join along with these modelers in pondering airbrushing?</p>
          </div>
        </section>

        <section className="section">
          <div className="container">
            <h1 className="title has-text-centered">Maker X Modeler: Discussing Airbrushes</h1>
            <p>When it comes to painting a plastic model via airbrush, you generally hear the advice that you should "thin your paints and lower your air pressure", right? And there's plenty of people who do that. In an attempt to re-examine this theory, and to find a different method for crafting beautiful models, the editorial team invited three modelers who have worked with this magazine before. Apart from discussing about how they reached their current painting style, we'll be examining the various companies involved with modeling paints.</p>
            <p>Why not join along with these modelers in pondering airbrushing?</p>
          </div>
        </section>

        <section className="section">
          <div className="container">
            <h2 className="subtitle has-text-centered">Introducing the participants:</h2>
            <h3>keita</h3>
            <p>Kansai's biggest of big shots, he's a famous modeler you hear about even on social media. He debuted in this magazine working with Frame Arms Girls, and afterwards he was assigned to work on Gunpla (his specialty) as well. His MG V2 Assault Buster Gundam adorned the cover of our February 2019 issue, and he served as judge in the January 2020 issue's All Japan Ora-Zaku Championship.</p>
            <h3>nishi</h3>
            <p>One of Kansai's leading modelers, he's an old friend of keita's. He's often been assigned to work with character models, primarily female plastic models like Frame Arms Girls. His skill is such that even in kit reviews, his naturally beautiful craftsmanship shines through and imparts something special on his works. Aside from model kits, he's a multi-talented modeler who's had his figures serve as finished samples.</p>
            <h3>urahana3</h3>
            <p>Uniquely, she's a housewife modeler. Aside from her amazingly clean craftsmanship, she carriers her experiences with her and uses her acquired modeling skill to create numerous original works. In this magazine, she mainly works with female plastic models. She's graced the front cover of MOOK's Hobby Japan Extra many times. And even in this magazine, her Frame Music Girls Hatsune Miku was on our front cover.</p>
            <h3>Kentarou</h3>
            <p>A modeler since childhood, he works on this magazine's How To articles. He's actually a multi-talented modeler who's worked with both character figures and scale models. This time around, he'll be representing the orthodox style.</p>
            <h3>Masked 13</h3>
            <p>A mysterious(?) masked man who's appeared many times in this magazine, and is very well-acquainted with paint. He was invited as a representative of the company Gaia Notes, and will be giving a lecture on airbrush paint.</p>
            <h3>Kondou Kyouta</h3>
            <p>He'll be representing the airbrush company Airtex. He's very knowledgable about airbrushes, and can be found at booths related to airbrushes at hobby events. Aside from that, he has plenty of experience repairing and using them. He could teach you the fundamental knowledge of an airbrush he's never seen before as if he were an expert.</p>
          </div>
        </section>

        <hr />

        <section className="section">
          <div className="container">
            <p><b>Today, we invited everyone here to ask if there's a different way to paint than the "theory" we see so often in painting models. The one about thinning your paint, and then spraying countless light coats at a low pressure to build up color.</b></p>
            <p>Keitarou: If I had to pick a side, I'd go with the theory. But on the other hand, I've heard that you all paint with thick paints and high air pressure. Considering that you can even find guidebooks that recommend thinning your paints at a ratio of about 1 part paint to 3 parts thinner, I'd like to ask how you arrived at your present method.</p>
          </div>
        </section>

        <section className="section">
          <div className="container">
            <h2 className="subtitle has-text-centered">A difference in thinner effectiveness and the introduction of high pressure air compressors.<br />Cleanly covering edges in one coat.</h2>
            <p>Keita: To be honest, I thin by intuition, not calculation, so I can't really speak that much on the subject, but for me I think the change started with my compressor. In the beginning, I used Tamiya's HG Compressor Revo. Even if I wanted to raise the air pressure, I couldn't. I think it outputted around 0.08 Mpa? Back then, I used GSI Creos' Mr. Color paints like it was a matter of course, and also their yellow label Leveling Thinner as the solvent. Shopkeepers recommended them to me so I bought them, and just as Kentarou-san said, it was written in guidebooks to paint with a 1:3 paint-to-thinner ratio. After I did that, the paint was so watery that I couldn't spray it that well, so I  gradually thickened it. Back then I sprayed at about 1:2. I was even questioned about it when I was in the Net Modelers (a Gunpla builder team), "why do you have the paint that thick?" I also asked around a lot, and found that back then most people didn't use Leveling Thinner, they used the ordinary thinner. So I figured "Man~, I guess the thinning ratio changes if you use ordinary thinner". And then after that I met Finisher's.</p>
            <p>With Finisher's paint, if you tried to use it the same way as the Mr. Color paints of the time, it was thick. It was finer, and yet thick. When you sprayed it with an airbrush, the noise changed. The <i>shaaa</i> became a <i>shubaaa</i>... Despite that, even though it was paint at a thickness I'd never sprayed before, it sprayed just fine. It had a shine to it, and its hiding power was novel. In the old days, you'd have to spray countless coats to lay down white. But with Finisher's Foundation White, just two coats would hide the base color. I was like, "what the hell?".  From then on I started mainly using thick paints, and if I found a compressor that could output higher pressure than I switched to it. And so today, even though my dilution ratio has gotten thicker than ever, I can spray it.</p>
            <p>This is changing the subject, but I primarily use Tamiya's spray cans for my surfacer. It goes without saying, but with a spraycan you can't adjust the pressure like with an airbrush, right? But if you use it right, you can paint cleanly thanks to the surface tension from blasting out all that thick paint at once. Typically a lot of the paint will stick to the majority of the surface fine, but it won't adhere that well at the edges. But if your spraying technique is good, the edges will stand strong even after painting. So what if I brought that reasoning to airbrushes...? Working off that question to myself, I did a lot of trial-and-error to figure out how to spray easier. I ended up thinning with Gaia Notes' Brush Master at around 1:2.</p>
            <p>With that said, recently I've changed my preferences to Quattroporte's Tipo Multi Thinner, and now I can spray even thicker. I'd say around 1:1.5 to 1:1.7 or so? 1:1 makes me go "that's thick". I have to consider the condition of the paint too, which can differ depending on the color. Red turns watery if I add too much thinner, and blue is similarly at risk. White is fine. As a rule I don't mention numbers like this, I just leave it to instinct. Listen to the sound when it sprays, look at the smoothness of the paint surface... Well, that's going too far. I have a rule of thumb paint-to-thinner ratio, I just change it up every now and then.</p>
          </div>
        </section>

        <section className="section">
          <div className="container">
            <h2>There's a fixed thickness to a proper coat of paint.<br />Spray it all at once to work faster.</h2>
            <p>nishi: ...Wait, keita-san said everything already (laughs). I've known him for a while, so we've shared information.</p>
            <p>I'm fundamentally not very good at processes like layering coats of paint. I'm all about shortening work time. Simply put, if you don't dilute your paint as much, you can finish in less coats. As far as layering methods go, it's easy to produce differences in color. I think there's a fixed thickness to the paint at the point when the color has been laid down properly. Whether you sprayed 5 coats or 1 coat, if the color is the same then the thickness is the same. So in that case, can't you finish faster by spraying thicker concentrations? As for clean finishes or glossy surfaces, I've achieved both of those in practice, so I think I have a feel for what a suitable thickness should be.</p>
            <p>What keita-san mentioned about spray cans applies to me too, but I'd like to point out that when you paint, the edges get covered too. So if you layer multiple coats, all of that's building up on the edges and gradually killing it. So to avoid that, I think you should paint all at once so that the edges won't get coated more than necessary. And there's that, gap filling trick, right? When you spray surfacer and then sand it off [to fill in defects on the part]. I never do that either. No matter how carefully you do it, there will be parts of the edges you can't reach. They're sure to die.</p>
            <p>Other than that, I'm about the same as keita-san, I think? The change in paint too. Before 2005 I used the old Mr. Color jars with convex caps, and then various things changed with Finisher's paint. I had to stick with Mr. Color for painting gradients, though, because the hiding power of Finisher's paint was too strong. So you could say keita-san and I had our painting styles specialize into flat coats.</p>
            <p>keita: I know it's nishi-san's turn, but I used to fill gaps with surfacer. It was to repair the damage to the plastic surface I caused by sanding with 240 grit sandpaper. I thought it was fine back then. I didn't care as much about precise details as I do now. Kits back then were usually plain too, so I used painting techniques involving gradients like the MAX method. The whole concept of trying to produce clean edges basically didn't exist. But as kits started becoming as highly detailed as they are now, the two of us started gradually shifting to flat coats. Based on the geometry of the surface, you can use a variety of colors to express a single color. Taking white as an example, the spots with shadow are gray, the spots with lighter shadow are light gray, and the bright spots are white. If you paint a gradient, doesn't that niceness just die away? I think that's how we ended up with our current painting style.</p>
            <p>nishi: I... have nothing to add. I think the same.</p>
            <p>keita: Sorry for talking so much.</p>
            <p>nishi: You always just say everything, huh. If I don't say anything, I feel like I'll get treated as a postscript. (laughs)</p>
            <p><b>But nishi-san, you've made figures that were treated as finished samples, right? Was your painting style any different then?</b></p>
            <p>nishi: Oh it's completely different. Flesh tones have to be thinned until they're watery, because they're being sprayed out of a 0.2 mm or 0.18 mm nozzle. As you'd expect, the method has to change to meet the situation.</p>
            <p>Kentaurou: So would you say paint needs to be thinned to match the airbrush's nozzle?</p>
            <p>nishi: That and to match the painting method. Normally, I use an 0.3 mm, a 0.2 mm, and a 0.18 mm. They're for painting shading. Actually, the painting method can change depending on the color. For example, with paints like cobalt blue that have weak hiding power, if the base color isn't sky blue or purple, I always thin at a 1:1 ratio. Also, here's something where keita-san and I differ. When I'm painting figures I mix colors, but keita-san never mixes his paint. He's part of the faction that uses paint straight out of the jar. Though I'm admittedly the same with pastel colors like emerald green that are hard to mix.</p>
          </div>
        </section>

        <section className="section">
          <div className="container">
          </div>
        </section>

      </main>

      <hr />
      
      <section>
        <footer>The content on this website was based off of the feature article <i>"New common sense of airbrush 2020"</i> of the February 2020 edition of <i>Hobby Japan</i>.</footer>
      </section>

    </body>
  );
}